html,
body,
div,
span,
applet,
object,
iframe,
h1,
h2,
h3,
h4,
h5,
h6,
p,
blockquote,
pre,
a,
abbr,
acronym,
address,
big,
cite,
code,
del,
dfn,
em,
img,
ins,
kbd,
q,
s,
samp,
small,
strike,
strong,
sub,
sup,
tt,
var,
b,
u,
i,
center,
dl,
dt,
dd,
ol,
ul,
li,
fieldset,
form,
label,
legend,
table,
caption,
tbody,
tfoot,
thead,
tr,
th,
td,
article,
aside,
canvas,
details,
embed,
figure,
figcaption,
footer,
header,
hgroup,
menu,
nav,
output,
ruby,
section,
summary,
time,
mark,
audio,
video {
  margin: 0;
  padding: 0;
  border: 0;
  font-size: 100%;
  vertical-align: baseline;
}
/* HTML5 display-role reset for older browsers */
article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section {
  display: block;
}
body {
  line-height: 1;
}
ol,
ul {
  list-style: none;
}
blockquote,
q {
  quotes: none;
}
blockquote:before,
blockquote:after,
q:before,
q:after {
  content: '';
  content: none;
}
table {
  border-collapse: collapse;
  border-spacing: 0;
}
button {
  cursor: pointer;
}
button,
input,
textarea {
  border-radius: 0;
  -webkit-appearance: none;
}
b,
strong {
  font-weight: bold;
}
i,
em {
  font-style: italic;
}
a {
  text-decoration: none;
}
.cf:before,
.cf:after {
  content: " ";
  display: table;
}
.cf:after {
  clear: both;
}
.cf {
  *zoom: 1;
}
.no-transition {
  -webkit-transition: none !important;
  -moz-transition: none !important;
  -o-transition: none !important;
  transition: none !important;
}
html {
  height: 100%;
  min-height: 100%;
}
body {
  font-family: "HelveticaNeue", Arial, sans-serif;
  font-size: 12px;
  height: 100%;
  min-height: 100%;
}
a {
  text-decoration: none;
}
a:hover,
a:active,
a:focus {
  text-decoration: none;
}
form label {
  display: block;
  font-size: 12px;
  color: #171717;
  font-family: 'HelveticaNeue-Medium', sans-serif;
  font-weight: normal;
  line-height: 18px;
  text-transform: uppercase;
  margin-bottom: 2px;
}
form input {
  height: 46px;
  border: solid 1px #e6e6e9;
  width: 100%;
  padding-left: 14px;
  margin-bottom: 14px;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  border-radius: 3px;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
}
form textarea {
  width: 100%;
  border: none;
  -webkit-border-radius: 3px;
  -moz-border-radius: 3px;
  -ms-border-radius: 3px;
  -o-border-radius: 3px;
  border-radius: 3px;
  -moz-background-clip: padding;
  -webkit-background-clip: padding-box;
  background-clip: padding-box;
  resize: none;
  line-height: 22px;
  padding: 12px;
  box-sizing: border-box;
  margin-bottom: 14px;
  display: block;
}
.button {
  font-size: 16px;
  color: #fff;
  font-family: "HelveticaNeue-Medium", sans-serif;
  margin-right: 8px;
  margin-left: 8px;
  text-align: center;
  cursor: pointer;
}
@media only screen and (max-width: 380px) {
  .button {
    margin: 10px 0px;
  }
}
.button.center {
  margin: auto;
  display: block;
}
/*MESSAGES*/
.alert {
  padding: 1em 2em;
  margin-bottom: 20px;
  text-shadow: 0 1px 0 rgba(255, 255, 255, 0.5);
  background-color: #fcf8e3;
  border: 1px solid #fbeed5;
  border-radius: 0em;
  color: #c09853;
  line-height: 1.3em;
  font-size: 12px;
}
.alert.alert-success {
  color: #468847;
  background-color: #dff0d8;
  border-color: #d6e9c6;
}
.alert.alert-error {
  color: #B80300;
  background-color: #FCB2B2;
  border-color: #FF8080;
}
.alert.alert-info {
  color: #3a87ad;
  background-color: #d9edf7;
  border-color: #bce8f1;
}
/*MESSAGES END*/
.right {
  float: right;
}
.left {
  float: left;
}
