}
.input-box-defaults {
  display: block;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
.maximum-width {
  max-width: 870px;
}
.button {
