input {
  -webkit-transition: all 400ms;
  -moz-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
  outline: none;
}
input[type='text'],
input[type='password'] {
  display: block;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
input[type='submit'] {
  font-size: 16px;
  color: #fff;
  font-family: "HelveticaNeue-Medium", sans-serif;
  margin-right: 8px;
  margin-left: 8px;
  text-align: center;
  border: none;
  border-radius: 5px;
  padding: 10px;
  color: #555;
  background: #ddd;
  cursor: pointer;
  min-width: 100px;
  margin-left: 0px;
  margin-right: 0px;
}
@media only screen and (max-width: 380px) {
  input[type='submit'] {
    margin: 10px 0px;
  }
}
input[type='submit'].center {
  margin: auto;
  display: block;
}
input[type='submit'].remove-item {
  background-color: rgba(242, 68, 27, 0.3);
}
input[type='submit']:hover {
  color: white;
  background: #555;
}
input[type='checkbox'] {
  -webkit-appearance: checkbox !important;
}
input:hover,
input:focus {
  box-shadow: 0 0 9px -3px rgba(0, 0, 0, 0.5);
}
textarea,
select {
  display: block;
  box-sizing: border-box;
  padding: 10px;
  margin-bottom: 20px;
  border: 1px solid #ddd;
  border-radius: 5px;
}
select {
  -webkit-appearance: menulist;
}
label {
  display: block;
  text-transform: uppercase;
  font-size: 0.9em;
  color: #888888;
  margin-bottom: 5px;
  margin-left: 10px;
}
.add-button {
  background: #777;
  padding: 9px 25px;
  border-radius: 4px;
  color: white;
  font: bold 14px "HelveticaNeue", Arial, sans-serif;
  display: inline-block;
}
.add-button:hover {
  color: white;
  background: #555;
}
.fileUpload {
  background: #777;
  padding: 9px 25px;
  border-radius: 4px;
  color: white;
  font: bold 14px "HelveticaNeue", Arial, sans-serif;
  display: inline-block;
  -webkit-transition: all 400ms;
  -moz-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
  position: relative;
  overflow: hidden;
  margin-bottom: 20px;
}
.fileUpload:hover {
  color: white;
  background: #555;
}
.fileUpload input {
  position: absolute;
  top: 0;
  right: 0;
  margin: 0;
  padding: 0;
  font-size: 20px;
  cursor: pointer;
  opacity: 0;
  filter: alpha(opacity=0);
  font-family: "HelveticaNeue", 'Montserrat', sans-serif;
}
.login-page {
  position: fixed;
  width: 100%;
  height: 100%;
}
.login-page form {
  text-align: center;
}
.login-page label {
  margin-left: auto;
}
header {
  position: fixed;
  width: 200px;
  height: 100%;
  left: 0;
  top: 0;
  background: #292929;
  z-index: 5;
}
header .logo {
  display: block;
  text-align: center;
  margin: 30px 20px;
}
header .logo img {
  width: 160px;
}
header .subtitle {
  font-size: 10px;
  text-align: center;
  text-transform: uppercase;
  letter-spacing: 5px;
  margin-bottom: 30px;
}
header .header-menu ul.account {
  position: absolute;
  bottom: 0;
  width: 100%;
}
header .header-menu li {
  -webkit-transition: all 400ms;
  -moz-transition: all 400ms;
  -o-transition: all 400ms;
  transition: all 400ms;
  font-size: 1em;
  line-height: 1.4em;
  text-transform: uppercase;
  border-top: 1px solid white;
  position: relative;
  display: block;
  z-index: 5;
}
header .header-menu li a {
  display: block;
  padding: 10px 20px;
  color: #a2a2a2;
}
header .header-menu li:hover,
header .header-menu li.active {
  background: white;
  border-color: #ddd;
}
header .header-menu li:hover a,
header .header-menu li.active a {
  color: #080808;
}
header .header-menu li:hover .submenu {
  display: block;
}
header .header-menu li:last-child {
  border-bottom: 1px solid white;
}
header .header-menu .submenu {
  display: none;
  margin-left: 0px;
  top: -1px;
  position: absolute;
  right: -91%;
  background: #292929;
  box-sizing: border-box;
}
header .header-menu .submenu li a {
  color: #a2a2a2;
}
header .header-menu .submenu li a:hover {
  color: #080808;
}
